import { Component, Injectable, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import {SchemaCacheService} from '../../services/schema-cache.service';
import {WalkingeditService} from '../../services/walkingedit.service';
import _ from 'lodash';


@Injectable()
@Component({
  selector: 'app-walkingdata-script-editor',
  templateUrl: './walkingdata-script-editor.component.html'
})
export class WalkingdataScriptEditorComponent implements OnInit {
  @ViewChild('scripteditor') scripteditor: any;
  constructor(private schema: SchemaCacheService,
              private editing: WalkingeditService) {
    const self = this;
    self.scriptConfig.extraKeys = {
      Esc: (cm) => self.apply()
    };
  }
  options: any = {};
  editorOptions: any = {};
  tabs = [];
  currentTab: any = {};
  opened = false;
  scriptConfig: any = {
    lineNumbers: true,
    lineWrapping: true,
    mode: 'javascript',
    theme: 'blackboard',
    height: '280px'
  };
  ngOnInit() {
    const self = this;
    self.opened = false;
    self.editing.scriptEditor.show = (o) => {
      self.editorOptions = o;
      self.init();
      self.opened = true;
      setTimeout(() => {
        const doc = self.scripteditor.instance.doc;
        const li = doc.lastLine();
        const ll = doc.getLine(li);
        self.scripteditor.instance.setCursor({
          line: li,
          ch: ll.length
        });
        self.scripteditor.instance.focus();
      }, 250);
    };
    // TODO: cerca .CodeMirror-vscrollbar e ci aggiunge la classe echo-scrollbar
  }
  private init() {
    const self = this;
    const fieldsTab = {
      title: 'Fields & Params',
      fields: true,
      icon: 'view_list'
    };
    self.tabs.splice(0);
    self.tabs.push(fieldsTab);
    if (self.editorOptions.connection) {
      self.schema.connection(self.editorOptions.connection);
      self.tabs.push({
        title: 'Database',
        database: true,
        icon: 'view_agenda'
      });
    }
    if (self.editorOptions.functions) {
      self.tabs.push({
        title: 'Functions',
        functions: true,
        icon: 'functions'
      });
    }
    self.tabs.push({
      title: 'Help',
      help: true,
      icon: 'help'
    });
    this.currentTab = fieldsTab;
  }
  apply() {
    if (_.isFunction(this.editorOptions.apply)) {
      this.editorOptions.apply();
    }
    this.close();
  }
  close() {
    this.opened = false;
    this.tabs.splice(0);
    this.editorOptions = {};
  }
  openTab(tab) {
    this.currentTab = tab;
  }
  onInsert(v) {
    if (!this.scripteditor) { return; }
    const doc = this.scripteditor.instance.getDoc();
    doc.replaceSelection(v);
    this.scripteditor.instance.focus();
  }
  onInsertField(field) {
    const v = this.editorOptions.format(field || {});
    this.onInsert(v);
  }
  onInsertFunction(func) {
    const f = func.template || (func.name + '()');
    this.onInsert(f);
  }
}


@Component({
  selector: 'app-script-editor-help',
  templateUrl: './wd-script-editor-help.component.html'
})
export class WalkingdataScriptEditorHelpComponent implements OnInit {
  @Input('key') key: string;
  constructor() {
  }
  ngOnInit() {
  }
}



