import {Injectable} from '@angular/core';
import {UtilsService} from './utils.service';
import {ParameterEditorComponent} from '../components/parameter-editor/parameter-editor.component';
import {ParameterValuesComponent} from '../components/parameter-values/parameter-values.component';
import {ValueEditorComponent} from '../components/value-editor/value-editor.component';
import {WarningsEditorComponent} from '../components/warnings-editor/warnings-editor.component';
import {WalkingdataWarning} from './walkingdata.service';
import {MatDialog} from '@angular/material';
import _ from 'lodash';


@Injectable()
export class DialogsService {

  constructor(private u: UtilsService,
              private dialog: MatDialog) { }
  editParameter(p) {
    const self = this;
    const pe = self.dialog.open(ParameterEditorComponent, {
      width: '500px',
      data: {
        parameter: self.u.clone(p)
      }
    });
    pe.afterClosed().subscribe(op => {
      _.extend(p, op || {})
      p.icon = this.u.getParameterIcon(p);
    });
  }
  editParameters(cll, cb) {
    const self = this;
    const pe = self.dialog.open(ParameterValuesComponent, {
      width: '500px',
      data: {
        parameters: cll
      }
    });
    pe.afterClosed().subscribe((resp) => resp ? cb() : _.noop());
  }
  editValue(v) {
    const self = this;
    const pe = self.dialog.open(ValueEditorComponent, {
      width: '500px',
      data: {value: self.u.clone(v)}
    });
    pe.afterClosed().subscribe(ov => _.extend(v, ov || {}));
  }
  editWarnings(warnings: Array<WalkingdataWarning>, cb) {
    const self = this;
    const pe = self.dialog.open(WarningsEditorComponent, {
      width: '700px',
      data: warnings
    });
    pe.afterClosed().subscribe((resp) => {
      if (resp) {
        warnings.forEach((oi: WalkingdataWarning) => oi.resolve());
        cb();
      }
    });
  }
}



