import { Injectable } from '@angular/core';
import {WalkingdataService} from './walkingdata.service';
import {BrowserService} from './browser.service';
import {MatDialog} from '@angular/material';
import {WalkingdataSourceEditorComponent} from '../components/walkingdata-source-editor/walkingdata-source-editor.component';
import {WalkingdataTargetEditorComponent} from '../components/walkingdata-target-editor/walkingdata-target-editor.component';
import _ from 'lodash';


@Injectable()
export class WalkingeditService {

  constructor(private walking: WalkingdataService,
              private dialog: MatDialog,
              private browser: BrowserService) { }
  scriptEditor: any = {
    show: () => {}
  };
  editSource(source?) {
    if (source) {
      this.walking.setCurrentSource(this.browser.current, source);
    }
    this.dialog.open(WalkingdataSourceEditorComponent, {
      width: '800px',
      data: { }
    });
  }
  editTarget(target?) {
    if (target) {
      this.walking.currentTarget = target;
    }
    this.dialog.open(WalkingdataTargetEditorComponent, {
      width: '800px',
      data: { }
    });
  }
}



