import { Component, OnInit, Input } from '@angular/core';
import {AppComponent} from '../../app.component';
import {WalkingdataService} from '../../services/walkingdata.service';
import {WalkingeditService} from '../../services/walkingedit.service';
import {UtilsService} from '../../services/utils.service';
import {BrowserService} from '../../services/browser.service';
import {SchemaCacheService} from '../../services/schema-cache.service';
import {InteractionService} from '../../services/interaction.service';
import _ from 'lodash';




@Component({
  selector: 'app-walkingdata-transformer',
  templateUrl: './walkingdata-transformer.component.html'
})
export class WalkingdataTransformerComponent implements OnInit {
  @Input('target') target: any;
  @Input('field') field: any;
  constructor(public walking: WalkingdataService,
              public editing: WalkingeditService,
              private app: AppComponent,
              private u: UtilsService,
              private interaction: InteractionService,
              private browser: BrowserService,
              private cache: SchemaCacheService) { }
  transformerMenu = [{
    caption: 'Expression',
    code: 'expression',
    icon: 'settings_ethernet'
  }, {
    caption: 'Lookup',
    code: 'lookup',
    advanced: false,
    icon: 'link'
  }, {
    caption: 'Lookup Advanced',
    code: 'lookup',
    advanced: true,
    icon: 'link'
  }];
  connections = [];
  schema = null;
  table = null;
  ngOnInit() {
    const self = this;
    self.walking.checkTransformer(self.field.transformer);
    self.connections = _(self.browser._documents)
      .filter(d => d._type === self.interaction.types.connections)
      .value();
    self.loadSchema();
  }
  loadTable() {
    const self = this;
    self.table = {};
    if (!self.field.transformer.lookup.advanced && (self.field.transformer.lookup || {}).tableName) {
      self.table.columns = _.filter(self.schema.columns, c => c.TABLE_NAME === self.field.transformer.lookup.tableName);
      self.table.name = self.field.transformer.lookup.tableName;
    }
  }
  loadField() {
    const self = this;
    if ((self.field.transformer.lookup || {}).fieldMatch && self.table) {
      const col = _.find(self.table.columns, c => c.COLUMN_NAME === self.field.transformer.lookup.fieldMatch);
      if (col) {
        self.field.transformer.lookup.fieldType = col.DATA_TYPE;
      }
    }
  }
  loadSchema() {
    const self = this;
    if ((self.field.transformer.lookup || {}).connection) {
      self.cache.getSchema(self.field.transformer.lookup.connection)
        .then(schema => {
          self.schema = schema
          self.loadTable();
        });
    }
  }
  execVoice(v) {
    if (v.code) {
      this.walking.setTransformerMode(this.field.transformer,  v);
    }
  }
  isChecked(v) {
    return v.code === this.field.transformer.mode &&
      (this.field.transformer.mode !== 'lookup' || v.advanced === (this.field.transformer.lookup || {}).advanced);
  }
  private include(items: any[], cll: any[], typedef: any) {
    if ((cll || []).length) {
      items.push(typedef);
      cll.forEach(i => i._type = typedef.type);
      items.push.apply(items, cll);
    }
  }
  editTransformer() {
    const self = this;
    if ((self.target || {})._editor === false) { return; }
    const advanced = self.field.transformer.lookup.advanced;
    const items = [];
    self.include(items, self.walking.currentSource.schema, {name: 'Fields', divider: true, type: 'f'});
    self.include(items, self.browser.current.parameters, {name: 'Parameters', divider: true, type: 'p'});
    const options: any = {
      fields: items,
      apply: () => {}
    };
    const targetdesc = 'Target field: {name}\n\nexpected type: {typecode}';

    switch (self.field.transformer.mode) {
      case 'lookup':
        const info = advanced ? self.field : {
          name: self.field.transformer.lookup.fieldMatch,
          typecode: self.field.transformer.lookup.fieldType
        };
        options.mode = 'sql';
        options.helpKey = advanced ? 'lookup-advanced' : 'lookup';
        options.title = advanced ? 'Lookup script' : 'Lookup filter';
        options.description = self.u.format(targetdesc, info);
        options.script = self.field.transformer.lookup.filter;
        options.connection = self.field.transformer.lookup.connection;
        options.apply = () => self.field.transformer.lookup.filter = options.script;
        options.format = (f) => '{=' + f._type + '[\'' + f.name + '\']}';
        break;
      default:
        options.mode = 'js';
        options.helpKey = 'expression';
        options.title = 'Transformer expression';
        options.functions = (this.app.info.walking || {}).functions || [];
        options.description = self.u.format(targetdesc, self.field);
        options.script = self.field.transformer.expression;
        options.apply = () => self.field.transformer.expression = options.script;
        options.format = (f) => f._type + '[\'' + f.name + '\']';
        break;
    }
    self.editing.scriptEditor.show(options);
  }
}



@Component({
  selector: 'app-walkingdata-target',
  templateUrl: './walkingdata-target.component.html'
})
export class WalkingdataTargetComponent implements OnInit {
  @Input('target') target: any;
  constructor(public walking: WalkingdataService,
              private editing: WalkingeditService,
              private u: UtilsService) { }

  ngOnInit() {
    this.walking.checkHandler(this.target);
  }
  toggle() {
    this.target._collapsed = !this.target._collapsed;
  }
  deleteTarget() {
    const self = this;
    self.u.confirm({
      message: 'Do you want to delete this target?',
      ok: 'Yes',
      cancel: 'No'
    }, data => {
      if (data.resp === 'ok') {
        _.pull(self.walking.currentSource.targets, self.target);
      }
    });
  }
  editTarget() {
    this.editing.editTarget(this.target);
  }
  toggleAdvanced() {
    this.target._editor = _.isBoolean(this.target._editor) ? !this.target._editor : false;
  }
}



