import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-walkingdata-help',
  templateUrl: './walkingdata-help.component.html'
})
export class WalkingdataHelpComponent implements OnInit {
  constructor() {}
  map = [{
    id: 'help-walkingdata',
    desc: 'Walking-data'
  }, {
    id: 'help-walkingdata-handlers',
    desc: 'Gestori delle risorse'
  }, {
    id: 'help-walkingdata-editor',
    desc: 'Expressions editor'
  }, {
    id: 'help-walkingdata-actions',
    desc: 'Azioni possibili'
  }];
  ngOnInit() {
  }
}



