import { Component, OnInit, OnDestroy, Input, ElementRef, ViewChild } from '@angular/core';
import {WalkingdataService} from '../../services/walkingdata.service';
import {UtilsService} from '../../services/utils.service';
import {BrowserService} from '../../services/browser.service';
import {InteractionService} from '../../services/interaction.service';
import {SchemaCacheService} from '../../services/schema-cache.service';
import _ from 'lodash';


@Component({
  selector: 'app-wd-handler-file-definition',
  templateUrl: './wd-handler-file-definition.component.html'
})
export class WalkingdataHandlerFileDefComponent implements OnInit {
  @Input('handler') handler: any;
  @ViewChild('fileInput') fileInput: ElementRef;
  constructor(public walking: WalkingdataService,
              private u: UtilsService) {
  }
  lines = [];
  error = null;
  file = null;
  uploading = false;
  onFileChange(e) {
    const self = this;
    if (e.target.files && e.target.files.length > 0) {
      if (self.handler.type === 'file') {
        self.uploading = true;
        self.walking.loadFile(self.handler, e.target.files[0], () => {
          self.uploading = false;
        });
      }
    }
  }
  ngOnInit() {
  }
  reload(e) {
    // TODO: ricarica il file dal server
    this.u.snack('Not implemented yet!');
  }
  invokeClick(e) {
    const event = new MouseEvent('click', {bubbles: true});
    this.fileInput.nativeElement.dispatchEvent(event);
  }
}


@Component({
  selector: 'app-wd-handler-query-definition',
  templateUrl: './wd-handler-query-definition.component.html'
})
export class WalkingdataHandlerQueryDefComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService,
              private interaction: InteractionService,
              private browser: BrowserService) {
  }

  queries = [];

  private parseSchema(query) {
    return _.map(query.columns, c => {
      return {
        name: c.name,
        type: c.type
      };
    });
  }

  setSchema() {
    const self = this;
    if (self.handler.query) {
      const query = _.find(self.queries, q => q._id === self.handler.query);
      self.interaction.getDocument(query)
        .subscribe((q: any) => {
          self.walking.loadSchema(self.handler, self.parseSchema(q));
          self.handler.name = q.name;
        }, err => {
          console.error(err);
        });
    }
  }

  ngOnInit() {
    const self = this;
    self.queries = _(self.browser._documents)
      .filter(d => d._type === self.interaction.types.queries)
      .value();
  }
}


@Component({
  selector: 'app-wd-handler-table-definition',
  templateUrl: './wd-handler-table-definition.component.html'
})
export class WalkingdataHandlerTableDefComponent implements OnInit, OnDestroy {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService,
              private u: UtilsService,
              private interaction: InteractionService,
              private browser: BrowserService,
              private cache: SchemaCacheService) {
  }
  initializing = false;
  canBulk = false;
  connections: any = [];
  providers = [];
  schema = null;
  connection = null;
  provider = null;
  error = null;
  options: any = {};
  private parseSchema(table) {
    const self = this;
    const cols = _((self.schema || {}).columns || [])
      .filter(c => c.TABLE_NAME === table.TABLE_NAME && c.TABLE_CATALOG === table.TABLE_CATALOG && c.TABLE_SCHEMA === table.TABLE_SCHEMA)
      .value();
    return _.map(cols, c => {
      return {
        name: c.COLUMN_NAME,
        type: c.DATA_TYPE
      };
    });
  }

  onConnection() {
    const self = this;
    if (self.handler.connection) {
      self.cache.getSchema(self.handler.connection).then(schema => self.schema = schema);
      self.connection = _.find(self.connections, (c) => c._id === self.handler.connection);
      self.provider = self.connection ? _.find(self.providers || [], (p) => p.code === self.connection.provider) : null;
    }
  }

  ngOnInit() {
    const self = this;
    self.initializing = true;
    self.canBulk = !!(self.handler || {}).target && self.walking.isBulkSource(self.walking.currentSource);
    self.u.compose()
      .use((next) => {
        self.interaction.getDocuments(self.interaction.types.connections)
          .subscribe(connections => {
            self.connections = connections;
            next();
          }, err =>  {
            self.error = err;
            next();
          });
      })
      .use((next) => {
        self.interaction.getProviders().subscribe(providers => {
          if (_.keys(providers || {}).length) {
            self.providers = _.filter(_.map(_.keys(providers), k => providers[k]), p => p.active);
          }
          next()
        }, (err) => {
          self.error = err;
          next();
        });
      })
      .run(() => {
        self.onConnection();
        self.initializing = false;
      });
    self.options.editable = self.handler.target && self.walking.isType(self.handler, ['newtable']);
  }

  ngOnDestroy() {
    const self = this;
    if (self.options.editable) {
      self.handler.name = self.handler.tableName;
    }
  }

  setSchema() {
    const self = this;
    const table = _.find((self.schema || {}).tables || [], (t) => t.TABLE_NAME === self.handler.tableName);
    self.walking.loadSchema(self.handler, self.parseSchema(table));
    self.handler.name = self.handler.tableName;
  }
}


@Component({
  selector: 'app-wd-handler-web-definition',
  templateUrl: './wd-handler-web-definition.component.html'
})
export class WalkingdataHandlerWebDefComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService) {
  }
  methods = ['GET', 'POST'];
  ngOnInit() {
    this.handler.url = this.handler.url || 'http://';
    this.handler.method = this.handler.method || 'GET';
    if (!_.isBoolean(this.handler.auth)) { this.handler.auth = false; }
    this.handler.authUser = this.handler.authUser || '';
    this.handler.authPswd = this.handler.authPswd || '';
    this.handler.params = this.handler.params || '';
    this.handler.body = this.handler.body || '';
  }
}


@Component({
  selector: 'app-wd-handler-rules',
  templateUrl: './wd-handler-rules.component.html'
})
export class WalkingdataHandlerRulesComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService) {
  }
  rules = [];
  ngOnInit() {
    const self = this;
    self.rules = _.filter(self.walking.rules, r =>
      (!r.targets || r.targets.indexOf(self.handler.type) > -1) &&
      (!r.contexts || r.contexts.indexOf(self.handler.mode) > -1));
  }
  addRule(rule) {
    this.walking.addRule(this.handler, rule);
  }
  removeRule(rule) {
    _.pull(this.handler.rules, rule);
  }
}


@Component({
  selector: 'app-wd-handler-schema-field',
  templateUrl: './wd-handler-schema-field.component.html'
})
export class WalkingdataHandlerSchemaFieldComponent implements OnInit {
  @Input('handler') handler: any;
  @Input('field') field: any;
  constructor(public walking: WalkingdataService) {
  }
  options: any = {};
  index = 0;
  aligns = ['left', 'right'];
  types = [];
  private isType(types) {
    return (types.indexOf(this.handler.type) > -1);
  }
  ngOnInit() {
    const isfixed = (this.handler.textMode === 'fixed');
    this.types = this.walking.types;
    this.index = this.handler.schema.indexOf(this.field) + 1;
    this.options.editable = this.isType(['newfile', 'newtable', 'file']) || this.handler.target;
    this.options.len = this.isType(['newfile', 'file']) && isfixed;
    this.options.align = this.isType(['newfile', 'file']) && this.handler.target && isfixed;
  }
}


@Component({
  selector: 'app-wd-handler-schema',
  templateUrl: './wd-handler-schema.component.html'
})
export class WalkingdataHandlerSchemaComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService,
              private u: UtilsService) {
  }
  options: any = {};
  ngOnInit() {
    this.options.editable = this.walking.isType(this.handler, ['newfile', 'newtable', 'file']) || this.handler.target;
  }
  select(field) {
    this.handler._field = field;
  }
  add() {
    const nf = this.walking.getNewField(this.handler);
    this.handler.schema.push(nf);
    this.handler._field = nf;
  }
  remove() {
    _.pull(this.handler.schema, this.handler._field);
    this.handler._field = null;
  }
}


