@Component({
  selector: 'app-wd-lineeditor-fixed',
  templateUrl: './wd-lineeditor.component.html'
})
export class WalkingdataLineEditorFixedComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService,
              private u: UtilsService) {
  }
  o: any = {
    preline2: {text: '', n: 0},
    preline1: {text: '', n: 0},
    postline1: {text: '', n: 0},
    postline2: {text: '', n: 0},
    ready: false
  };
  private getParts(h) {
    const self = this;
    let i = h.start;
    if (i < 0) { i = 0; }
    const line = (h.lines || [])[i] || '';
    const linedata = {
      titles: h.headers ? h.lines[0] || '' : line,
      values: line
    };
    const parts = [];
    (h.schema || []).forEach((c, i) => {
      let len = c.length || 1;
      if (len < 1) { len = 1; }
      const txt = linedata.titles.substr(0, len);
      const val = linedata.values.substr(0, len);
      parts.push({
        title: txt,
        text: val,
        type: self.u.decodeType(val, h),
        index: i
      });
      linedata.titles = linedata.titles.slice(len);
      linedata.values = linedata.values.slice(len);
    });
    if (linedata.titles.length) {
      parts.push({
        title: linedata.titles,
        text: linedata.values,
        type: self.u.decodeType(linedata.values, h),
        index: parts.length
      });
    }
    (_.last(parts) || {}).last = true;
    return parts;
  }
  private readLines() {
    const self = this;
    self.walking.readLines(self.handler, self.o, self);
  }
  regen() {
    const self = this;
    const txt = self.walking.getSelectedText() || '';
    if (txt.length > 0 && txt.length < 100) {
      const f = {
        name: self.u.getNewName(self.handler.schema, 'New Field'),
        type: 'string',
        length: txt.length,
        align: 'left'
      };
      self.walking.checkField(f);
      self.handler.schema.push(f);
      this.readLines();
    }
  }
  prev() {
    this.handler.start--;
    this.readLines();
  }
  next() {
    this.handler.start++;
    this.readLines();
  }
  ngOnInit() {
    this.handler.schema = this.handler.schema || [];
    this.readLines();
  }
  select(part) {
    if ((this.handler.schema || []).length > part.index) {
      this.handler._field = this.handler.schema[part.index];
    }
  }
}


@Component({
  selector: 'app-wd-lineeditor-separator',
  templateUrl: './wd-lineeditor.component.html'
})
export class WalkingdataLineEditorSeparatorComponent implements OnInit {
  @Input('handler') handler: any;
  constructor(public walking: WalkingdataService,
              private u: UtilsService) {
  }
  o: any = {
    preline2: {text: '', n: 0},
    preline1: {text: '', n: 0},
    postline1: {text: '', n: 0},
    postline2: {text: '', n: 0},
    ready: false
  };
  private getParts(h, separators: boolean = false) {
    const _split = (sep, v) => {
      // TODO: deve tener conto dei campi testo!! (delimitati da ")
      // const testsrgx = /(["'])(?:(?=(\\?))\2.)*?\1/g;
      return v.split(sep);
    };
    const self = this;
    let i = h.start;
    if (i < 0) { i = 0; }
    const line = (h.lines || [])[i] || '';
    const parts = [];
    const linedata = {
      titles: _split(h.separator, h.headers ? h.lines[0] || '' : line),
      values: _split(h.separator, line)
    };
    linedata.titles.forEach((p, i) => {
      if (parts.length && separators) {
        parts.push({text: h.separator, separator: true});
      }
      parts.push({
        title: p,
        text: linedata.values[i],
        type: self.u.decodeType(p, h),
        index: i
      });
    });
    (_.last(parts) || {}).last = true;
    return parts;
  }
  private readLines() {
    const self = this;
    self.walking.readLines(self.handler, self.o, self);
    if (self.o.ready && !(this.handler.schema || []).length) {
      this.regen();
    }
  }
  regen() {
    const self = this;
    self.handler.schema.splice(0);
    self.getParts(self.handler).forEach(p => {
      const f = {
        name: p.title || '',
        type: p.type || 'string'
      };
      self.walking.checkField(f);
      self.handler.schema.push(f);
    });
  }
  prev() {
    this.handler.start--;
    this.readLines();
  }
  next() {
    this.handler.start++;
    this.readLines();
  }
  ngOnInit() {
    this.handler.schema = this.handler.schema || [];
    this.readLines();
  }
  select(part) {
    if ((this.handler.schema || []).length > part.index) {
      this.handler._field = this.handler.schema[part.index];
    }
  }
}


