///////////////////////////////////////////////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////
// HANDLER MOVER
///////////////////////////////////////////////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////

const DIRECTION = {
  up: 'up',
  right: 'right',
  down: 'down',
  left: 'left'
};

@Component({
  selector: 'app-wd-handler-mover',
  templateUrl: './wd-handler-mover.component.html'
})
export class WalkingdataHandlerMoverComponent implements OnInit {
  @Input('stage') stage: any;
  constructor(public walking: WalkingdataService,
              private browser: BrowserService) {
  }
  direction = DIRECTION;
  locked = {
    up: false,
    left: false,
    right: false,
    down: false
  };
  ngOnInit() {
    const self = this;
    self.walking.onSource.subscribe(() => self.refresh());
  }
  refresh() {
    this.locked.up = (this.browser.current.stages.indexOf(this.stage) === 0);
    this.locked.left = (this.stage.sources.indexOf(this.walking.currentSource) === 0);
    this.locked.right = (this.stage.sources.indexOf(this.walking.currentSource) === (this.stage.sources.length - 1));
    this.locked.down = (this.browser.current.stages.indexOf(this.stage) === (this.browser.current.stages.length - 1));
  }
  move(direction) {
    const source_pos = this.stage.sources.indexOf(this.walking.currentSource);
    const stage_pos = this.browser.current.stages.indexOf(this.stage);
    let target_pos = -1;
    let target_stage = this.stage;
    switch (direction) {
      case DIRECTION.up:
        target_stage = this.browser.current.stages[stage_pos - 1];
        break;
      case DIRECTION.down:
        target_stage = this.browser.current.stages[stage_pos + 1];
        break;
      case DIRECTION.left:
        target_pos = source_pos - 1;
        break;
      case DIRECTION.right:
        target_pos = source_pos + 1;
        break;
    }
    if (target_stage) {
      const source = this.stage.sources.splice(source_pos, 1)[0];
      (target_pos < 0) ? target_stage.sources.push(source) : target_stage.sources.splice(target_pos, 0, source);
      this.walking.setCurrentSource(this.browser.current, source);
    } else {
      this.refresh();
    }
  }
}





