import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { CartService } from '../../services/cart.service';
import {UtilsService} from '../../services/utils.service';
import {WalkingdataService, WalkingdataWarning} from '../../services/walkingdata.service';
import {WalkingeditService} from '../../services/walkingedit.service';
import {DialogsService} from '../../services/dialogs.service';
import _ from 'lodash';




@Component({
  selector: 'app-walkingdata-targets',
  templateUrl: './walkingdata-targets.component.html'
})
export class WalkingdataTargetsComponent implements OnInit {
  constructor(public dialogRef: MatDialogRef<WalkingdataTargetsComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              public walking: WalkingdataService) {
    this.selected = this.walking.targets[0];
  }
  selected = null;
  select(btn) {
    if (this.selected === btn) {
      this.dialogRef.close(this.selected);
    } else {
      this.selected = btn;
    }
  }
  ngOnInit() {
  }
}

@Component({
  selector: 'app-walking',
  templateUrl: './walkingdata.component.html'
})
export class WalkingdataComponent implements OnInit, OnDestroy {

  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private cart: CartService,
              public walking: WalkingdataService,
              private editing: WalkingeditService,
              private dialog: MatDialog,
              private u: UtilsService,
              private dialogs: DialogsService) {
    const self = this;
    self.browser.register({
      type: self.interaction.types.walkingdata,
      onSelect: () => {
        self.walking.reset();
        if (!self.browser.current) { return; }
        self.browser.current.stages = self.browser.current.stages || [];
        self.walking.checkWalking(self.browser.current);
      },
      onLoaded: (docs) => {
        // on loaded
      },
      template: {
        name: 'New Walking-data',
        info: '',
        stages: [{
          sources: []
        }],
        parameters: [],
        active: true
      }
    });
    self.header = {
      title: () => (self.browser.currentInfo || {}).name,
      isNew: () => (self.browser.current || {})._new,
      buttons: [{
        icon: 'play_arrow',
        tooltip: 'launch this walking-data',
        visible: () => !!self.browser.current && !self.walking.idle,
        action: () => self.checkAndRun()
      }, {
        icon: 'stop',
        tooltip: 'stop this walking-data',
        color: 'warn',
        visible: () => !!self.browser.current && self.walking.current === self.browser.current._id && self.walking.idle,
        action: () => self.walking.stop()
      }, {
        divider: true
      }, {
        icon: 'add_circle_outline',
        tooltip: 'Add a new walking-data',
        visible: () => !!self.browser.current,
        action: () => self.browser.newElement()
      }, {
        icon: 'save',
        tooltip: 'Save modifies',
        visible: () => !!self.browser.current,
        action: () => self.browser.save('Walking-data successfully updated!')
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove walking-data',
        color: 'warn',
        visible: () => !!self.browser.current,
        action: () => self.browser.delete('Delete current walking-data?')
      }]
    };
  }
  header = {};
  ready = false;
  private run() {
    const self = this;
    self.walking.run({
      walking: self.browser.current,
      parameters: []
    });
  }
  checkAndRun() {
    const self = this;
    const warnings: Array<WalkingdataWarning> = [];
    self.browser.current.stages.forEach((stage) => {
      stage.sources.forEach((s) => {
        s.check(warnings);
        s.targets.forEach((t) => {
          t.check(warnings);
        });
      });
    });
    self.u.compose()
      .use((next) => {
        warnings.length ? self.dialogs.editWarnings(warnings, next) : next();
      })
      .use((next) => {
        (self.browser.current.parameters || []).length ? self.dialogs.editParameters(self.browser.current.parameters, next) : next();
      })
      .run(() => self.run());
  }
  ngOnInit() {
    this.ready = true;
  }
  ngOnDestroy() {
  }
  newParameter() {
    const name = this.u.getNewName(this.browser.current.parameters, 'New Parameter');
    const np = this.u.getNewParameter(name);
    this.browser.current.parameters.push(np);
  }
  edit(p) {
    this.dialogs.editParameter(p);
  }
  remove(p) {
    _.pull(this.browser.current.parameters, p);
  }
  clearLog() {
    this.walking.clear();
  }
  deleteSource() {
    const self = this;
    const stage = _.find(self.browser.current.stages, (s) => !!_.find(s.sources, h => h === self.walking.currentSource));
    self.u.confirm({
      message: 'Do you want to delete the source?',
      ok: 'Yes',
      cancel: 'No'
    }, data => {
      if (data.resp === 'ok') {
        _.pull(stage.sources, self.walking.currentSource);
        self.walking.reset();
      }
    });
  }
  editSource() {
    this.editing.editSource();
  }
  addSourceTarget() {
    const self = this;
    const dlg = self.dialog.open(WalkingdataTargetsComponent, {
      width: '500px',
      data: { }
    });
    dlg.afterClosed().subscribe(target => {
      if (_.isObject(target)) {
        const source = self.walking.currentSource;
        source.targets = source.targets || [];
        const s = self.walking.isType(target, ['newfile', 'newtable']) ? source : null;
        const t = self.walking.getNewTarget(target, s);
        source.targets.push(t);
        self.editing.editTarget(t);
      }
    });
  }
  newStage() {
    const self = this;
    self.browser.current.stages.push({
      sources: []
    });
  }
}



