import {Directive, HostListener, HostBinding, EventEmitter, Output, Input} from '@angular/core';
import _ from 'lodash';

@Directive({
  selector: '[dragdrop]'
})
export class DragdropDirective {
  @Input() private extensions: Array<string> = [];
  @Output() private filesChangeEmiter: EventEmitter<File[]> = new EventEmitter();
  @HostBinding('style.background') private background = 'transparent';

  constructor() { }
  private state(s?) {
    this.background = s ? '#7b1fa224' : 'transparent';
  }
  private ext(filename) {
    const spl = filename.split('.');
    return spl[spl.length - 1];
  }
  @HostListener('dragover', ['$event']) public onDragOver(e) {
    e.preventDefault();
    e.stopPropagation();
    this.state('active');
  }

  @HostListener('dragleave', ['$event']) public onDragLeave(e) {
    e.preventDefault();
    e.stopPropagation();
    this.state();
  }

  @HostListener('drop', ['$event']) public onDrop(e) {
    e.preventDefault();
    e.stopPropagation();
    const self = this;
    self.state();
    const filtered = (self.extensions || []).length > 0;
    const files = _(e.dataTransfer.files)
      .filter(f => !filtered || self.extensions.indexOf(self.ext(f.name)) > -1)
      .value();
    self.filesChangeEmiter.emit(files);
  }
}



