import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {WalkingdataService} from '../../services/walkingdata.service';

@Component({
  selector: 'app-walkingdata-source-editor',
  templateUrl: './walkingdata-source-editor.component.html'
})
export class WalkingdataSourceEditorComponent implements OnInit, OnDestroy {

  constructor(public dialogRef: MatDialogRef<WalkingdataSourceEditorComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              public walking: WalkingdataService) { }
  source: any = {};
  tabs = [{
    title: 'DEFINITION',
    definition: true
  }, {
    title: 'SCHEMA',
    schema: true
  }, {
    getTitle: () => {
      const rc = (this.source.rules || []).length;
      return 'RULES' + (rc ? ' (' + rc + ')' : '');
    },
    rules: true
  }];
  currentTab = null;
  ngOnInit() {
    const self = this;
    self.currentTab = self.tabs[0];
    self.source = self.data.source || self.walking.currentSource;
    self.walking.checkHandler(self.source);
  }
  ngOnDestroy() {
    this.walking.checkHandler(this.source);
  }
  openTab(tab) {
    this.currentTab = tab;
  }
  validate() {
    const self = this;
    return self.walking.validate(self.source);
  }
  close() {
    this.dialogRef.close();
  }
}



