import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {WalkingdataService} from '../../services/walkingdata.service';

@Component({
  selector: 'app-walkingdata-target-editor',
  templateUrl: './walkingdata-target-editor.component.html'
})
export class WalkingdataTargetEditorComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<WalkingdataTargetEditorComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              public walking: WalkingdataService) { }
  target: any = {};
  tabs: Array<any> = [{
    title: 'DEFINITION',
    definition: true
  }, {
    title: 'SCHEMA',
    schema: true
  }, {
    getTitle: () => {
      const rc = (this.target.rules || []).length;
      return 'RULES' + (rc ? ' (' + rc + ')' : '');
    },
    rules: true
  }];
  currentTab = null;
  ngOnInit() {
    const self = this;
    this.currentTab = self.tabs[0];
    self.target = self.data.target || self.walking.currentTarget;
    this.walking.checkHandler(self.target);
  }
  openTab(tab) {
    this.currentTab = tab;
  }
  validate() {
    const self = this;
    return self.walking.validate(self.target);
  }
  close() {
    this.dialogRef.close();
  }
}



