import { Component, OnInit, Input, Inject } from '@angular/core';
import {WalkingdataService} from '../../services/walkingdata.service';
import {WalkingeditService} from '../../services/walkingedit.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {UtilsService} from '../../services/utils.service';
import {BrowserService} from '../../services/browser.service';
import _ from 'lodash';


@Component({
  selector: 'app-walkingdata-handlers',
  templateUrl: './walkingdata-handlers.component.html'
})
export class WalkingdataHandlersComponent implements OnInit {
  constructor(public dialogRef: MatDialogRef<WalkingdataHandlersComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              public walking: WalkingdataService) {
    this.selected = this.walking.sources[0];
  }
  selected = null;
  select(btn) {
    if (this.selected === btn) {
      this.dialogRef.close(this.selected);
    } else {
      this.selected = btn;
    }
  }
  deleteStage() {
    this.dialogRef.close('delete');
  }
  ngOnInit() {
  }
}


@Component({
  selector: 'app-walkingdata-handler',
  templateUrl: './walkingdata-handler.component.html'
})
export class WalkingdataHandlerComponent implements OnInit {
  constructor(public walking: WalkingdataService) {}
  @Input('source') source: any;
  ngOnInit() {
  }
}


@Component({
  selector: 'app-walkingdata-stage',
  templateUrl: './walkingdata-stage.component.html'
})
export class WalkingdataStageComponent implements OnInit {
  @Input('stage') stage: any;
  @Input('index') index: number;
  constructor(public walking: WalkingdataService,
              private editing: WalkingeditService,
              private u: UtilsService,
              private dialog: MatDialog,
              private browser: BrowserService) {
  }
  uploading = false;
  ngOnInit() {
  }
  onFilesChange(files) {
    const self = this;
    if ((files || []).length > 0) {
      self.uploading = true;
      self.stage.sources = self.stage.sources || [];
      const source = _.find(self.walking.sources, (src) => src.type === 'file');
      const s = self.walking.getNewSource(source);
      self.walking.loadFile(s, files[0], () => {
        self.stage.sources.push(s);
        self.uploading = false;
        self.editing.editSource(s);
      });
    }
  }
  open(e) {
    const self = this;
    const dlg = self.dialog.open(WalkingdataHandlersComponent, {
      width: '500px',
      data: { }
    });
    dlg.afterClosed().subscribe(source => {
      if (_.isObject(source)) {
        self.stage.sources = self.stage.sources || [];
        const s = self.walking.getNewSource(source);
        self.stage.sources.push(s);
        self.editing.editSource(s);
      } else if (_.isString(source)) {
        switch (source) {
          case 'delete':
            const onclear = self.browser.current.stages.length < 2;
            self.u.confirm({
              message: onclear ? 'All sources will be deleted, do you want to continue?' :
                'Stage and all its sources will be deleted, do you want to continue?',
              ok: 'Yes',
              cancel: 'No'
            }, data => {
              if (data.resp === 'ok') {
                if (self.walking.currentSource && self.stage.sources.indexOf(self.walking.currentSource) > -1) {
                  self.walking.reset();
                }
                if (onclear) {
                  self.stage.sources.splice(0);
                } else {
                  _.pull(self.browser.current.stages, self.stage);
                }
              }
            });
            break;
        }
      }
    });
  }
  selectSource(e, source) {
    const self = this;
    e.stopPropagation();
    self.walking.setCurrentSource(self.browser.current, source);
  }
}



