const DATATYPES = ['string', 'number', 'integer', 'date', 'bool', 'object'];
const HANDLER_TYPES = {
  file: 'file',
  newfile: 'newfile',
  table: 'table',
  newtable: 'newtable',
  query: 'query',
  web: 'web'
};
const DEFAULTS = {
  textMode: 'separator',
  textDelimiter: '"',
  separator: ';',
  mode: {
    target: 'target',
    source: 'source'
  }
};
const TEXTTYPES = {
  xml: 'xml',
  json: 'json',
  fixed: 'fixed',
  separator: 'separator'
};

const NEWELEMENTPOLICY = {
  error: 'error',           // mostra errore
  overwrite: 'overwrite',   // sovrascrive quello presente
  append: 'append',         // accoda il risultato
  rename: 'rename'          // ne crea uno con nome diverso
};


export class WalkingdataWarning {
  constructor() {}
  message = '';
  resolve() {
    // TODO: risolve lo warning proposto
  }
}

export class WalkingdataSourceEvent {
  constructor(source) {
    this.source = source;
  }
  source: any = null;
}

@Injectable()
export class WalkingdataService implements OnDestroy {

  constructor(private user: UserSettingsService,
              private interaction: InteractionService,
              private wmanager: WalkingdataWarningsService,
              private u: UtilsService) {
    this.connect();
  }
  private _socket = null;
  onSource: EventEmitter<WalkingdataSourceEvent> = new EventEmitter();
  idle = false;
  current: any = null;
  currentSource: any = null;
  currentStage: any = null;
  currentTarget: any = null;
  items = [];
  targets = TARGETS;
  sources = SOURCES;
  rules = RULES;
  types = DATATYPES;
  defaults = DEFAULTS;
  newPolicies = [{
    code: NEWELEMENTPOLICY.error,
    desc: 'Throw an error'
  }, {
    code: NEWELEMENTPOLICY.append,
    desc: 'Append results in existent'
  }, {
    code: NEWELEMENTPOLICY.overwrite,
    desc: 'Overwrite'
  }, {
    code: NEWELEMENTPOLICY.rename,
    desc: 'Write with other name'
  }];
  textTypes = [{
    name: TEXTTYPES.xml,
    desc: 'Xml file',
    check: (h) => {
      if (h.text50.trim()[0] === '<') {
        h.textMode = TEXTTYPES.xml;
        return true;
      }
    }
  }, {
    name: TEXTTYPES.json,
    desc: 'Json text file',
    check: (h) => {
      if (['[', '{'].indexOf(h.text50.trim()[0]) > -1) {
        h.textMode = TEXTTYPES.json;
        return true;
      }
    }
  }, {
    name: TEXTTYPES.fixed,
    desc: 'Fields of fixed length',
    check: (h) => {
      const resp = {};
      let num = 0;
      (h.lines || []).forEach(line => {
        if ((line || '').length) {
          resp[line.length] = resp[line.length] || 0;
          resp[line.length]++;
          num++;
        }
      });
      // ritorna vero se le righe valide sono pi di 2 e almeno la met hanno la stessa lunghezza
      if (num > 2 && !!_.find(_.keys(resp), k => resp[k] >= (num / 2))) {
        h.textMode = TEXTTYPES.fixed;
        return true;
      }
    }
  }, {
    name: TEXTTYPES.separator,
    desc: 'Fields delimited by specific separators',
    check: (h) => {
      const rgx = /([,;|\t])/g;
      let num = 0;
      const resp = {};
      (h.lines || []).forEach(line => {
        if ((line || '').length) {
          const lnresp = {};
          (line.match(rgx) || []).forEach(m => {
            lnresp[m] = lnresp[m] || 0;
            lnresp[m]++;
          });
          const maxc = {c: '', n: 0};
          _.keys(lnresp).forEach(k => {
            if (lnresp[k] > maxc.n) {
              maxc.n = lnresp[k];
              maxc.c = k;
            }
          });
          if (maxc.n) {
            resp[maxc.c] = resp[maxc.c] || 0;
            resp[maxc.c]++;
          }
          num++;
        }
      });
      // ritorna vero se le righe valide sono pi di 2 e almeno la met utilizzano lo stesso separatore
      if (num > 2) {
        const sep = _.find(_.keys(resp), k => resp[k] >= (num / 2));
        if (sep) {
          h.separator = sep;
          h.textMode = TEXTTYPES.separator;
          return true;
        }
      }
    }
  }];
  private checkState() {
    const self = this;
    const started = _.find(self.items, (i) => i.type === 'start');
    const ended = _.find(self.items, (i) => i.type === 'end');
    self.current = self.current || (started || {}).message;
    self.idle = !!started && !ended;
  }
  private connect() {
    const self = this;
    if (!self._socket) {
      self.interaction.getWalkingState((err, states) => {
        if (err) { console.error(err); }
        const url = self.user.settings['host'];
        self.items.push.apply(self.items, states);
        self._socket = io(url);
        self._socket.on('walkingdata', (state) => {
          self.items.push(state);
          self.checkState();
        });
        self._socket.on('error', console.error);
        self.checkState();
      });
    }
  }
  private disconnect() {
    if (!!this._socket) {
      this._socket.disconnect();
      this._socket = null;
    }
  }
  private check(warnings: Array<WalkingdataWarning>) {
    // this.wmanager.check(this, warnings);
  }
  setCurrentSource(walking, source) {
    this.currentSource = source;
    this.currentStage = _.find(walking.stages, (s) => s.sources.indexOf(source) > -1);
    this.onSource.emit(new WalkingdataSourceEvent(source));
  }
  reset() {
    this.currentTarget = null;
    this.currentSource = null;
    this.currentStage = null;
  }
  run(o) {
    const self = this;
    if (self.idle) { return; }
    // TODO: popup parametri ....
    self.current = o.walking._id;
    self.items.splice(0);
    self.interaction.walk(o, (err) => {
      if (err) {
        self.u.error(err);
      } else {
        self.idle = true;
      }
    });
  }
  stop() {
    const self = this;
    self.interaction.stopWalk((err) => {
      if (err) {
        self.u.error(err);
      } else {
        self.idle = false;
      }
    });
  }
  clear() {
    const self = this;
    if (self.idle) { return; }
    self.current = null;
    self.items.splice(0);
  }
  getNewTarget(target, source?) {
    const self = this;
    const T = self.u.clone(target);
    T.mode = DEFAULTS.mode.target;
    T.target = true;
    T.separator = DEFAULTS.separator;
    T.textDelimiter = DEFAULTS.textDelimiter;
    T.start = 0;
    T.textMode = DEFAULTS.textMode;
    T.schema = [];
    T.check = self.check;
    T.bulk = false;
    if (source) {
      (source.schema || []).forEach(function(sf){
        const tf = self.getNewField(target, sf);
        T.schema.push(tf);
      });
    }
    return T;
  }
  getNewSource(source) {
    const self = this;
    const S = self.u.clone(source);
    S.mode = DEFAULTS.mode.source;
    S.source = true;
    S.start = 0;
    S.textMode = DEFAULTS.textMode;
    S.separator = DEFAULTS.separator;
    S.textDelimiter = DEFAULTS.textDelimiter;
    S.targets = [];
    S.check = self.check;
    return S;
  }
  private _convert(st, tt) {
    switch (st) {
      case 'string':
        switch (tt) {
          // case 'string': return '{0}';
          case 'date': return 'convert({0}, \'date\')';    // string to date
          case 'number': return 'convert({0}, \'number\')';    // string to number
          case 'bool': return 'convert({0}, \'bool\')';    // string to bool
          default: return '{0}';    // string to *
        }
      case 'date':
        switch (tt) {
          case 'string': return 'convert({0}, \'date\', \'yyyy-MM-dd\')';   // date to string
          // case 'date': return '{0}';   // date to date
          case 'number': return 'convert({0})';   //  date to number
          // case 'bool': return '{0}';   // date to bool
          default: return '';   // date to *
        }
      case 'number':
        switch (tt) {
          case 'string': return 'convert({0}, \'string\')';   // number to string
          case 'date': return 'convert({0}, \'date\')';   // number to date
          // case 'number': return '{0}';   // number to number
          case 'bool': return 'convert({0}, \'bool\')';   // number to bool
          default: return '{0}';   // number to *
        }
      case 'bool':
        switch (tt) {
          case 'string': return 'convert({0}, \'string\')';   // bool to string
          case 'date': return '';   // bool to date
          case 'number': return 'convert({0}, \'number\')';   // bool to number
          // case 'bool': return '{0}';   // bool to bool
          default: return '{0}';   // bool to *
        }
      default:
        switch (tt) {
          // case 'string': return '{0}';    // * to *
          // case 'date': return '{0}';    // * to *
          // case 'number': return '{0}';    // * to *
          // case 'bool': return '{0}';    // * to *
          default: return '{0}';    // * to *
        }
    }
  }
  private _buildFieldExp(sf, tft) {
    sf = sf || {};
    if (!sf.name) { return ''; }
    const convert_str = this._convert(sf.typecode, tft);
    return convert_str.replace(/\{0\}/g, 'f[\'' + sf.name + '\']');
  }
  isTextHandler(h) {
    return h.type === 'file' || h.type === 'newfile';
  }
  getNewField(handler, sf?) {
    const self = this;
    // target type code
    const ttc = self.isTextHandler(handler) ? 'string' : ((sf || {}).typecode || 'string');
    const f = {
      name: self.u.getNewName(handler.schema, (sf || {}).name || 'New Field'),
      type: self.isTextHandler(handler) ? 'string' : ((sf || {}).type || 'string'),
      typecode: ttc,
      transformer: {
        expression: self._buildFieldExp(sf, ttc)
      }
    };
    self.checkField(f);
    return f;
  }
  loadFile(handler, file, cb) {
    const self = this;
    self.interaction.uploadFile(file, (err, res) => {
      if (err) {
        handler.error = self.u.getErrorMessage(err);
        console.error(err);
        cb(err);
      } else {
        handler._file = file;
        handler.path = res.path;
        handler.name = file.name;
        handler.lines = ((res || {}).partial || {}).lines || [];
        handler.text50 = ((res || {}).partial || {}).text || '';
        self.decodeText(handler);
        cb(null, res);
      }
    });
    // const reader = new FileReader();
    // handler.error = null;
    // if (file) {
    //   handler._file = file;
    //   reader.onload = () => {
    //     console.log('FILE: ', handler._file);
    //     handler.path = handler._file.name;
    //     handler.name = handler._file.name;
    //     handler.lines = [];
    //     // prime 20 righe
    //     const text = '' + (reader.result || '');
    //     const rows = text.split('\n');
    //     let i = 0;
    //     while (handler.lines.length < 20 && i < rows.length) {
    //       const row = (rows[i++] || '').replace(/\r/g, '');
    //       if (row) {
    //         handler.lines.push(row);
    //       }
    //     }
    //     handler.text50 = ((text.substr(0, 500) || '') + '').trim();
    //     self.decodeText(handler);
    //   };
    //   reader.onerror = (err) => {
    //     handler.error = self.u.getErrorMessage(err);
    //     console.error(err);
    //   };
    //   reader.readAsText(handler._file);
    // }
  }
  addRule(handler, rule) {
    handler.rules = handler.rules || [];
    return (rule.unique && !!_.find(handler.rules, (r) => r.type === rule.type)) ?
      this.u.snack('This rule can only be added once!') :
      handler.rules.push(_.clone(rule));
  }


  checkWalking(walking) {
    const self = this;
    walking.stages.forEach((stage) => {
      stage.sources.forEach((s) => {
        self.checkHandler(s);
        s.targets.forEach((t) => {
          self.checkHandler(t);
        });
      });
    });
  }
  checkTransformer(t) {
    t.mode = t.mode || 'expression';
    t.lookup = t.lookup || {};
    if (!_.isBoolean(t.lookup.advanced)) { t.lookup.advanced = false; }
  }
  checkField(f) {
    const self = this;
    f.align = f.align || 'left';
    f.typecode = f.typecode || self.u.getDataType(f.type);
    f.transformer = f.transformer || {};
    f.icon = self.u.getTypeIcon(f.typecode || f.type);
    self.checkTransformer(f.transformer);
  }
  checkHandler(h) {
    const self = this;
    (h.schema || []).forEach(c => self.checkField(c));
    h.textMode = h.textMode || DEFAULTS.textMode;
    h.textDelimiter = h.textDelimiter || DEFAULTS.textDelimiter;
    h.separator = h.separator || DEFAULTS.separator;
    h.start = h.start || 0;
    h.check = self.check;
  }
  isType(handler, types) {
    return (types.indexOf(handler.type) > -1);
  }
  ngOnDestroy() {
    this.disconnect();
    this.items.splice(0);
  }
  decodeText(h) {
    if (!h.text50) {
      return console.warn('No available text on handler:', h);
    }
    const tt = _.find(this.textTypes, (type) => type.check(h));
    if (!tt) {
      return console.warn('Unrecognized text for handler:', h);
    }
  }
  readLines(h, o, owner) {
    o.ready = false;
    if (h.start < 0) { h.start = 0; }
    if ((h.lines || []).length) {
      if (h.start > (h.lines || []).length - 1) {
        h.start = (h.lines || []).length - 1;
      }
      o.parts = owner.getParts(h, true);
      o.preline2.n = (h.start > 1) ? h.start - 2 : '';
      o.preline1.n = (h.start > 0) ? h.start - 1 : '';
      o.postline1.n = (h.start < h.lines.length - 1) ? h.start + 1 : '';
      o.postline2.n = (h.start < h.lines.length - 2) ? h.start + 2 : '';
      o.preline2.text = (h.start > 1) ? h.lines[h.start - 2] : '';
      o.preline1.text = (h.start > 0) ? h.lines[h.start - 1] : '';
      o.postline1.text = (h.start < h.lines.length - 1) ? h.lines[h.start + 1] : '';
      o.postline2.text = (h.start < h.lines.length - 2) ? h.lines[h.start + 2] : '';
      o.ready = true;
    } else {
      h.start = 0;
    }
  }
  getSelectedText() {
    if (_.isFunction(window.getSelection)) {
      return window.getSelection().toString();
    } else if (_.isFunction(document.getSelection)) {
      return document.getSelection().toString();
    } else if (_.isFunction((document['selection'] || {}).createRange)) {
      return document['selection'].createRange().text;
    }
    return '';
  }
  setTransformerMode(transformer, v) {
    transformer.mode = v.code;
    transformer.lookup = transformer.lookup || {};
    transformer.lookup.advanced = !!v.advanced;
  }
  loadSchema(h, schema) {
    const self = this;
    h.schema = h.schema || [];
    h.schema = _.map(schema || [], c => {
      const f = _.clone(c);
      self.checkField(f);
      return f;
    });
  }
  // validazioni nell'editor del gestore
  validate(h) {
    if (!h) { return false; }
    switch (h.type) {
      case HANDLER_TYPES.table:
        return !!h.name && !!h.connection;
      case HANDLER_TYPES.newtable:
        return !!h.tableName && !!h.connection;
      case HANDLER_TYPES.file:
        return !!h.name;
      case HANDLER_TYPES.newfile:
        return !!h.name;
      case HANDLER_TYPES.query:
        return !!h.query;
      case HANDLER_TYPES.web:
        const rgx = /^http[s]?:\/\/[a-z0-9-._~\/?#@!%$&+=]+$/i;
        return !!h.url && rgx.test(h.url) && !!h.method;
      default: return true;
    }
  }
  isBulkSource(h) {
    return !!h && (h.type === HANDLER_TYPES.file || h.type === HANDLER_TYPES.newfile) && h.textMode === TEXTTYPES.separator;
  }
}



